<template>
  <div class="page-box__filter">
    <div class="page-box__filter__rooms">
      <h1>комнаты</h1>
      <div class="page-box__filter__room">
        <p><input class="page-box__filter__room_count" id="room-s" type="checkbox" value="XS" checked><label for="room-s">S</label></p>
        <p><input class="page-box__filter__room_count" id="room-1k" type="checkbox" value="1k"><label for="room-1k">1k</label></p>
        <p><input class="page-box__filter__room_count" id="room-2k" type="checkbox" value="2k"><label for="room-2k">2k</label></p>
        <p><input class="page-box__filter__room_count" id="room-3k" type="checkbox" value="3k"><label for="room-3k">3k</label></p>
      </div>
    </div>
    <div class="page-box__filter__slider">
      <h1>этаж</h1>
      <div class="page-box__filter__slider__values">
        <p id="page-box__filter__slider__values__min__floors"></p>
        <div class="spacer"></div>
        <p id="page-box__filter__slider__values__max__floors"></p>
      </div>
      <div id="page-box__filter__slider__floors"></div>
    </div>
    <div class="page-box__filter__slider">
      <h1>площадь, м<sup>2</sup></h1>
      <div class="page-box__filter__slider__values">
        <p id="page-box__filter__slider__values__min__area"></p>
        <div class="spacer"></div>
        <p id="page-box__filter__slider__values__max__area"></p>
      </div>
      <div id="page-box__filter__slider__area"></div>
    </div>
    <div class="page-box__filter__slider">
      <h1>стоимость, млн. р.</h1>
      <div class="page-box__filter__slider__values">
        <p id="page-box__filter__slider__values__min__price"></p>
        <div class="spacer"></div>
        <p id="page-box__filter__slider__values__max__price"></p>
      </div>
      <div id="page-box__filter__slider__price"></div>
    </div>
    <div class="page-box__filter__apply">
      <button @click="applyFilter">применить</button>
      <p id="clear-filter" @click="clearFilter">сбросить фильтр</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'FilterComponent',
  data: function () {
    return {
      rooms: [], // количество комнат
      floors: [], //[0] - мнимальный этаж, [1] - максимальный этаж
      area: [], //[0] - минимальная площадь, [1] - максимальная площадь
      price: [], // [0] - минимальная стоимость, [1] - максимальняа стоимость
      allCountRooms: document.getElementsByClassName('page-box__filter__room_count')
    }
  },
  methods: {
    applyFilter() {
      this.rooms = [];
      for (let countRooms of this.allCountRooms) {
        if (countRooms.checked) {
          this.rooms.push(countRooms.defaultValue)
        }
      }
      this.floors[0] = parseInt(document.getElementById('page-box__filter__slider__values__min__floors').innerText);
      this.floors[1] = parseInt(document.getElementById('page-box__filter__slider__values__max__floors').innerText);
      this.area[0] = parseFloat(document.getElementById('page-box__filter__slider__values__min__area').innerText);
      this.area[1] = parseFloat(document.getElementById('page-box__filter__slider__values__max__area').innerText);
      this.price[0] = parseFloat(document.getElementById('page-box__filter__slider__values__min__price').innerText) * 1000000;
      this.price[1] = parseFloat(document.getElementById('page-box__filter__slider__values__max__price').innerText) * 1000000;
      this.filter();
    },
    filter() {
      let filteredItems = this.$store.state.catalog.filter( item => {
        if (this.rooms.includes(item.short) && this.floors[0] <= item.floor && this.floors[1] >= item.floor && this.area[0] <= item.square && this.area[1] >= item.square && this.price[0] <= item.price_real && this.price[1] >= item.price_real){
          return true
        } else false
      });
      this.$store.commit('addFilteredItems', filteredItems);
    },
    clearFilter() {
      this.$store.commit('addFilteredItems', this.$store.state.catalog);
      for (let countRooms of this.allCountRooms) {
        countRooms.checked = false;
      }
    }
  },
  mounted: function (){
    this.$store.commit('addFilteredItems', this.$store.state.catalog);
  }
}
</script> 
