<template>
    <div class="page-box__rooms__room">
        <div class="page-box__rooms__room__head">
            <p class="page-box__rooms__room__head_floor">{{room.floor}} этаж</p>
            <p class="page-box__rooms__room__head_count-rooms">{{room.rooms}} комнат{{roomOrRooms()}}  -  {{room.square}}m<sup>2</sup></p>
        </div>
        <div class="page-box__rooms__room__map" :style="backgroundImg(room.plan)">
            <p class="page-box__rooms__room__map_number-room">№{{room.number}}</p>
        </div>
        <p class="page-box__rooms__room__price-full">{{spaceInPrice(room.price_real)}}р.</p>
        <p class="page-box__rooms__room__price-m2">{{spaceInPrice(room.price_real / room.square)}} р. за м<sup>2</sup></p>
        <button class="page-box__rooms__room_button">подробнее</button>
    </div>
</template>

<script>

export default {
  name: 'ItemComponent',
  props: ['room'],
  methods: {
      backgroundImg (url){
          return `background-image: url(${url})`
      },
      roomOrRooms () {
          if (this.room.rooms == 1) {
              return 'a'
          } else {
              return 'ы'
          }
      },
      spaceInPrice (price) {
          let priceToString = String(Math.trunc(price));
          let finishPrice = [];
          let countChar = 0;
          for (let i = priceToString.length - 1; i >= 0; i--) {
              if (countChar === 3) {
                  finishPrice.unshift(' ');
                  finishPrice.unshift(priceToString[i]);
                  countChar = 1;
              } else {
                  finishPrice.unshift(priceToString[i]);
                  countChar++;
              }
          }
          return finishPrice.join('');
      }
  }
}
</script>
